  height: 2px;
  border-radius: 10px;
  -webkit-transition: top .3s .3s, opacity 1s, -webkit-transform .3s;
  transition: top .3s .3s, opacity 1s, -webkit-transform .3s;
  transition: top .3s .3s, transform .3s, opacity 1s;
  transition: top .3s .3s, transform .3s, opacity 1s, -webkit-transform .3s;
}

.header .hamburger span:nth-child(1) {
  top: -2px;
}

.header .hamburger span:nth-child(2) {
  top: calc(50% - 2px);
}

.header .hamburger span:nth-child(3) {
  top: calc(100% - 2px);
}

.header .hamburger:hover span {
  background-color: #A7A7A7;
}

.header .hamburger.active span {
  -webkit-transition: top .3s, opacity .2s .2s, -webkit-transform .3s .3s;
  transition: top .3s, opacity .2s .2s, -webkit-transform .3s .3s;
  transition: top .3s, transform .3s .3s, opacity .2s .2s;
  transition: top .3s, transform .3s .3s, opacity .2s .2s, -webkit-transform .3s .3s;
}

.header .hamburger.active span:nth-child(1) {
  top: 50%;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.header .hamburger.active span:nth-child(2) {
  opacity: 0;
}

.header .hamburger.active span:nth-child(3) {
  top: 50%;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

